<template>
  <section class="mb-5">
    <div class="py-2 box mb-0 has-background-success is-size-7" v-if="hasCompletedDataFursona">
      <p class="has-text-white">
        <i class="fas fa-check fa-fw"></i>&nbsp;{{$t('profile.fursona.completed')}}
      </p>
    </div>
    <div class="py-2 box mb-0 has-background-warning is-size-7" v-else>
      <p class="has-text-black">
        <i class="fas fa-arrow-down fa-fw"></i>&nbsp;{{$t('profile.fursona.notCompleted')}}
      </p>
    </div>
      <div class="box">

        <div class="is-flex is-flex-direction-row is-justify-content-space-between is-clickable" @click="openSection">
          <p class="is-size-4 is-700">
            {{$t('profile.fursona.title')}}<span v-if="hasCompletedDataFursona">&nbsp;&nbsp;<i class="fas fa-check-circle has-text-success fa-fw"></i></span>
          </p>
          <p class="is-size-4">
            <i class="fas" :class="isOpened ? 'fa-angle-up' : 'fa-angle-down'"></i>
          </p>
        </div>
        <fieldset v-if="isOpened">
          <br>
          <ValidationObserver ref="observer" v-slot="{ invalid }">
            <div class="columns">
              <div class="column">
                <div class="field">
                  <label class="label">{{$t('profile.fursona.completeName')}} *</label>
                  <div class="control">
                    <ValidationProvider
                      :name="$t('profile.fursona.completeName')"
                      :rules="{required: true}"
                      v-slot="{errors}"
                    >
                      <input
                        type="text"
                        class="input is-medium"
                        v-model="fursonaFullname"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.fursona.badgeName')}} *</label>
                  <div class="control">
                    <ValidationProvider
                      :name="$t('profile.fursona.badgeName')"
                      :rules="{required: true}"
                      v-slot="{errors}"
                    >
                      <input
                        type="text"
                        class="input is-medium"
                        v-model="fursonaDisplayName"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.fursona.species')}} *</label>
                  <div class="control">
                    <ValidationProvider
                      :name="$t('profile.fursona.species')"
                      :rules="{required: true}"
                      v-slot="{errors}"
                    >
                      <input
                        type="text"
                        class="input is-medium"
                        v-model="fursonaSpecies"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.fursona.pronouns')}} *</label>
                  <div class="control">
                    <ValidationProvider
                      :name="$t('profile.fursona.pronouns')"
                      :rules="{required: true}"
                      v-slot="{errors}"
                    >
                      <input
                        type="text"
                        class="input is-medium"
                        v-model="fursonaPronoun"
                        :placeholder="$t('form.customPronouns')"
                      />
                      <span class="help">{{$t('form.pronounsDescription')}}</span>
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.fursona.aboutFursona')}} *</label>
                  <div class="control">
                    <ValidationProvider
                      :name="$t('profile.fursona.aboutFursona')"
                      :rules="{required: false, min: 3}"
                      v-slot="{errors}"
                    >
                      <textarea
                        v-model="fursonaDescription"
                        class="textarea"
                        rows="3"
                        :placeholder="$t('form.optional')"
                      ></textarea>
                      <p class="help">{{$t('profile.fursona.descriptions.descriptionAbout')}}</p>
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                 <div class="field">
                  <div class="control">
                    <div class="buttons">
                      <button
                        class="button is-primary"
                        :disabled="invalid || isSending"
                        :class="{'is-loading': isSending}"
                        @click="submit"
                      >
                        <span class="spaced">
                          <i class="fas fa-save"></i>
                          &nbsp;{{$t('form.save')}}
                        </span>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div class="column column por-dios-librame-de-esto is-12-touch is-narrow-desktop">
                <div class="box">
                    <h1 class="title is-4">{{$t('profile.fursona.avatarTitle')}}</h1>
                    <p>{{$t('profile.fursona.descriptions.helpAvatar')}}</p>
                    <br>
                    <Cropper :src="furryAvatarBase" ref="cropper" class="my-croppi" :canvas="theCanvas" :stencil-props="theStencil" @error="hasImageLoaded = false" @ready="hasImageLoaded = true"/>
                    <br>
                    <div class="field">
                      <button @click="$refs.avatarInput.click()"  class="button is-primary"><i class="fas fa-upload"></i>&nbsp;{{ $t('profile.fursona.changeAvatarButton') }}
                        <input type="file" ref="avatarInput" @change="uploadImage($event)" accept="image/jpg,image/jpeg,image/png" style="display:none"/>
                      </button>
                    </div>
                    <div class="field">
                      <button @click="sendProfilePicture" :disabled="!hasImageLoaded" :class="{'is-loading': isSendingFursonaAvatar}" class="button is-primary"><i class="fas fa-save"></i>&nbsp;{{$t('profile.saveButton')}}</button>
                    </div>
              </div>
            </div>
            </div>
          </ValidationObserver>
        </fieldset>
      </div>
  </section>
</template>

<script>
import { Cropper } from 'vue-advanced-cropper';
import 'vue-advanced-cropper/dist/style.css';

import userUtils from "@/mixins/userUtils";

export default {
  data() {
    return {
      isOpened: false,
      isSending: false,
      //data
      fursonaFullname: null,
      fursonaDisplayName: null,
      fursonaSpecies: null,
      fursonaPronoun: null,
      fursonaDescription: null,
      // customPronouns: false,
      // Avatar
      furryAvatar: 'https://api.furfiesta.com/user/avatar?' + (new Date() / 1),
      furryAvatarBase: null,
      givemedate: new Date() / 1,
      fursonaAvatar: null,
      hasImageLoaded: false,
      isSendingFursonaAvatar: false,
      theCanvas: {
        minHeight: 900,
        maxHeight: 900,
        maxWidth: 900,
        minWidth: 900
      },
      theStencil: { 
        aspectRatio: 1/1
      }

    };
  },
  created() {
    this.sync();
  },
  components: {
		Cropper
	},
  mixins: [userUtils],
  methods: {
    openSection: function(){
      this.isOpened = !this.isOpened;
      //refresh vue dom
      if(this.isOpened){
        this.$nextTick(() => {
          this.refreshCropper()
        });
      }
      // .nextTick(() => {
      // });
      // if(this.isOpened){
      //   this.refreshCropper()
      // }
    },
    sync: function() {
      this.fursonaFullname = this.getValue(this.user.fursonaFullname);
      this.fursonaDisplayName = this.getValue(this.user.fursonaDisplayName);
      this.fursonaSpecies = this.getValue(this.user.fursonaSpecies);
      this.fursonaPronoun = this.getValue(this.user.fursonaPronoun);
      this.fursonaDescription = this.getValue(this.user.fursonaDescription);
      this.givemedate = new Date() / 1
      this.refreshCropper()
    },
    refreshCropper: function () {
      this.$http.get(this.furryAvatar, {
        responseType: 'blob'
      })
      .then((res) => {
        let reader = new FileReader();
        reader.onload = () => this.furryAvatarBase = reader.result
        reader.readAsDataURL(res.data)
      })
      .catch((err) => {
        console.error(err)
      });
    },
    uploadImage(event) {
      // Reference to the DOM input element
      var input = event.target;
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader();
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.furryAvatarBase = e.target.result;
        };
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0]);
      }
    },
    sendProfilePicture() {
      this.isSendingFursonaAvatar = true
      const result = this.$refs.cropper.getResult();
      const payload = {
        fursonaAvatar: result.canvas.toDataURL("image/jpeg")
      };
      this.$http.post("/user/update", payload, {
        withCredentials: true
      })
        .then(() => this.updateUser())
        .then(() => {
          this.refreshCropper()
          this.updateTheAvatarGlobalTime()
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.isSendingFursonaAvatar = false;
        });
    },
    async submit() {
      const result = await this.$refs.observer.validate();
      if (!result) {
       this.$swal.fire({
          icon: 'warning',
          title: this.$t('profile.alertInvalidFields.title'),
          text: this.$t('profile.alertInvalidFields.text')
        })
        return;
      }
      this.startLoading();
      this.isSending = true;
      const payload = {
        fursonaFullname: this.fursonaFullname,
        fursonaDisplayName: this.fursonaDisplayName,
        fursonaSpecies: this.fursonaSpecies,
        fursonaPronoun: this.fursonaPronoun,
        fursonaDescription: this.fursonaDescription
      };
      if (this.imageLoaded) payload.fursonaAvatar = this.fursonaAvatar;
      this.$http
        .post("/user/update", payload)
         .then(() => this.updateUser())
        .then(() => this.sync())
        .then(() => {
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading();
          this.isSending = false;
        });
    },
  },
  computed: {}
};
</script>

<style lang="scss" scoped>
.avatar-frame{
  border-radius: 2000px;
  margin: 20px auto;
  box-shadow: 0 0 10px 0 #333333;
  border: 4px solid white;
  height:200px;
  width:200px;
}

.my-croppi{
  min-height: 300px;
  width: 100%;
}

