                >
                  <div class="select is-fullwidth">
                    <select v-model.lazy="hasFoodRestriction">
                      <option :value="null">{{$t('form.selectOne')}}</option>
                      <option value="none">{{$t('profile.healthInformation.foodRestrictionsOptions.none')}}</option>
                      <option value="celiac">{{$t('profile.healthInformation.foodRestrictionsOptions.celiac')}}</option>
