                v-slot="{errors}"
              >
                <label class="label">{{$t('profile.healthInformation.somethingElseTitle')}}</label>

                <div class="control">
                  <textarea class="textarea" v-model="somethingElse" rows="2"></textarea>
                </div>
                        <p class="help is-link"><i class="fas fa-arrow-up"></i>&nbsp;{{$t('profile.healthInformation.somethingElseDescription')}}</p>
