</template>

<script>
import userUtils from "@/mixins/userUtils";
export default {
  data() {
    return {
      isOpened: false,
      isSending: false,
      //data
      emergencyContact: null,
      emergencyPhone: null,
      hasMedsAllergies: null,
      customMedsAllergies: null,
      hasFoodAllergies: null,
      customFoodAllergies: null,
      hasOtherAllergies: null,
      customOtherAllergies: null,
      hasFoodRestriction: null,
      customFoodRestriction: null,
      doingMeds: null,
      customDoingMeds: null,
      somethingElse: null
    };
  },
  created() {
    this.sync();
  },
  mixins: [userUtils],
  methods: {
    sync: function() {
      this.emergencyContact = this.getValue(this.user.emergencyContact);
      this.emergencyPhone = this.getValue(this.user.emergencyPhone);
      this.hasMedsAllergies = this.getValue(this.user.hasMedsAllergies);
      this.customMedsAllergies = this.getValue(this.user.customMedsAllergies);
      this.hasFoodAllergies = this.getValue(this.user.hasFoodAllergies);
      this.customFoodAllergies = this.getValue(this.user.customFoodAllergies);
      this.hasOtherAllergies = this.getValue(this.user.hasOtherAllergies);
      this.customOtherAllergies = this.getValue(this.user.customOtherAllergies);
      this.hasFoodRestriction = this.getValue(this.user.hasFoodRestriction);
      this.customFoodRestriction = this.getValue(this.user.customFoodRestriction);
      this.doingMeds = this.getValue(this.user.doingMeds);
      this.customDoingMeds = this.getValue(this.user.customDoingMeds);
      this.somethingElse = this.getValue(this.user.somethingElse);
    },
    async submit() {
      const result = await this.$refs.observer.validate();
      if (!result) {
        this.$swal.fire({
          icon: 'warning',
          title: this.$t('profile.alertInvalidFields.title'),
          text: this.$t('profile.alertInvalidFields.text')
        })
        return;
      }
      this.startLoading();
      this.isSending = true;
      const payload = {
        emergencyContact: this.emergencyContact,
        emergencyPhone: this.emergencyPhone,
        hasMedsAllergies: this.hasMedsAllergies,
        customMedsAllergies: this.hasMedsAllergies ? this.customMedsAllergies : null,
        hasFoodAllergies: this.hasFoodAllergies,
        customFoodAllergies: this.hasFoodAllergies ? this.customFoodAllergies : null,
        hasOtherAllergies: this.hasOtherAllergies,
        customOtherAllergies: this.hasOtherAllergies ? this.customOtherAllergies : null,
        hasFoodRestriction: this.hasFoodRestriction,
        customFoodRestriction: this.isOptional(this.customFoodRestriction),
        doingMeds: this.doingMeds,
        customDoingMeds: this.doingMeds ? this.customDoingMeds : null,
        somethingElse: this.isOptional(this.somethingElse)
      };
      this.$http
        .post("/user/update", payload)
        .then(() => this.updateUser())
        .then(() => this.sync())
        .then(() => {
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading();
          this.isSending = false;
        });
    }
  }
};
</script>