import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoading: false,
    lastTimeUpdated: Date.now(),
    user: null
  },
  mutations: {
    updateUser (state, user){
      state.user = user
    },
    setLoading(state, loading) {
      // eslint-disable-next-line
      state.isLoading = loading;
    },
    updateTicketStatus(state, ticketStatus) {
      // eslint-disable-next-line
      if(ticketStatus === null) {
        Vue.set(state.user,'ticket', null)
        return
      }
      if(!state.user.ticket){
        let p = {
          status: ticketStatus.status,
          tier: ticketStatus.tier
        }
        Vue.set(state.user,'ticket', p)

      } else {
        Vue.set(state.user.ticket,'status', ticketStatus.status)
        Vue.set(state.user.ticket,'tier', ticketStatus.tier)
      }
    },
    updatePreviousTicketStatus(state, ticketStatus) {
      // eslint-disable-next-line
      if(!state.user.previousTicket){
        let p = {
          status: ticketStatus.status,
          tier: ticketStatus.tier
        }
        Vue.set(state.user,'previousTicket', p)
      } else {
        Vue.set(state.user.previousTicket,'status', ticketStatus.status)
        Vue.set(state.user.previousTicket,'tier', ticketStatus.tier)
      }
    },
  },
  getters: {
    getUser: (state) => state.user,
  },
  actions: {

  },
  modules: {
    
  }
})
