import store from '@/store';
import http from '@/http'

const userUtils = {
  data(){
    return {
      givemedate: new Date() / 1,
    }
  },
  methods: {
    updateUser: function () {
      // REQUIRED
      return http.get('/user')
        .then((response) => {
          store.commit('updateUser', response.data)
          return response
        })
        .catch((err) => {
          console.error(err)
          this.$swal.fire({
            icon: 'error',
            title: 'Oops..',
            text: 'Something happened'
          })
        });
    },
    checkFields(fieldsArray){
      if(!this.user) return false
      let valuesArray = fieldsArray.map( field => this.user[field] )
      return valuesArray.every(this.hasValue)
    },
    checkFieldsGuardian(fieldsArray){
      if(!this.user) return false
      if(!this.user.guardians) return false
      if(!this.hasGuardians[0]) return false
      let valuesArray = fieldsArray.map( field => this.hasGuardians[0][field] )
      return valuesArray.every(this.hasValue)
    },
    getTicketAvatar: tier => {
      switch(tier) {
        case 'general':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/886378572008b6e5a666c08d2cb5c326cf02799b.png'
        case 'sponsor':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/51449177e317e09d7055f8d5afbdcdfe3bb634a0.png'
        case 'supersponsor':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/da2f30ce06cd39e827e2a1ef660979ae1a5ada11.png'
        case 'staff':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/e9f635cc04f68e8462f3b27d2a7280079a9afb70.png'
        case 'awoo':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/0feb78383bba47e429a3ba68c442fd9442c125b0.png'
        case 'apollo':
          return 'http://new.furfiesta.com/wp-content/uploads/2023/01/c98b9ee56f838c9ca0f179665e596df9e65e2915.png'
        case 'upg_general_sponsor':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/51449177e317e09d7055f8d5afbdcdfe3bb634a0.png'
        case 'upg_general_supersponsor':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/da2f30ce06cd39e827e2a1ef660979ae1a5ada11.png'
        case 'upg_general_awoo':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/0feb78383bba47e429a3ba68c442fd9442c125b0.png'
        case 'upg_general_apollo':
          return 'http://new.furfiesta.com/wp-content/uploads/2023/01/c98b9ee56f838c9ca0f179665e596df9e65e2915.png'
        case 'upg_sponsor_supersponsor':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/da2f30ce06cd39e827e2a1ef660979ae1a5ada11.png'
        case 'upg_sponsor_awoo':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/0feb78383bba47e429a3ba68c442fd9442c125b0.png'
        case 'upg_sponsor_apollo':
          return 'http://new.furfiesta.com/wp-content/uploads/2023/01/c98b9ee56f838c9ca0f179665e596df9e65e2915.png'
        case 'upg_supersponsor_awoo':
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/0feb78383bba47e429a3ba68c442fd9442c125b0.png'
        case 'upg_supersponsor_apollo':
          return 'http://new.furfiesta.com/wp-content/uploads/2023/01/c98b9ee56f838c9ca0f179665e596df9e65e2915.png'
        case 'upg_awoo_apollo':
          return 'http://new.furfiesta.com/wp-content/uploads/2023/01/c98b9ee56f838c9ca0f179665e596df9e65e2915.png'
        default:
          return 'http://new.furfiesta.com/wp-content/uploads/2022/02/1c7a8eaf1a722d1bef9d90cb52295133d17b6e07.png'
      }
    },
    updateTheAvatarGlobalTime: function(){
      this.givemedate = new Date() / 1
    }
  },
  computed: {
    userProfileEnabled() {
      return process.env.VUE_APP_PROFILE_ENABLED === 'true';
    },
    isUserBlocked() {
      if(!this.user) return false
      if((this.user.ticket && (this.user.ticket.tier === 'blocked' || this.user.ticket.tier === 'refunded')) || this.user.banReason) return true
      return false
    },
    user: function(){
      return this.$store.getters['getUser']
    },
    userProfilePic: function(){
      if(!this.user) return ''
      return `${process.env.VUE_APP_API_URL}/user/avatar` 
    },
    userNames: function(){
      if(!this.user) return '' 
      return this.user.fursonaDisplayName ? this.user.fursonaDisplayName : this.user.names
    },
    userAge() {
      if(!this.user || !this.user.birthday) return null
      let theDayOfTheConvention = new Date(process.env.VUE_APP_ARFF_DATE_START);
      let birthDate =  new Date(this.user.birthday)
      let age = theDayOfTheConvention.getFullYear() - birthDate.getFullYear();
      let m = theDayOfTheConvention.getMonth() - birthDate.getMonth();
      if (m < 0 || (m === 0 && theDayOfTheConvention.getDate() < birthDate.getDate())) {
        age--;
      }
      return age;
    },
    hasTicket: function () {
      if (!this.user) return false
      if (!this.user.ticket) return false
      if(this.user.ticket && this.user.ticket.status === 'success') return true
      // if(this.user.rolloverTicket && this.user.rolloverTicket.status === 'success') return true
      if(this.user.ticket && (this.user.ticket.status === 'pending' || this.user.ticket.status === 'purchasing') && this.user.previousTicket && this.user.previousTicket.status === 'success') return true
      return false
    },
    hasConfirmedTicket: function(){
      if (!this.user) return false
      if (!this.user.ticket) return false
      if (this.hasTicket && this.user.confirmedTicket) return true
      return false
    },
    hasStartedTicketPurchase(){
      if(!this.user) return false
      if(this.user.ticket && (this.user.ticket.status === 'pending' || this.user.ticket.status === 'purchasing')) return true
      else if (this.user.ticket && this.user.ticket.status === 'success') return true
      return false
    },
    hasStartedTicketUpgrade(){
      if(!this.user) return false
      if(this.user.ticket && (this.user.ticket.status === 'pending' || this.user.ticket.status === 'purchasing') && this.user.ticket.tier.includes('upg_')) return true
      return false
    },
    hasComplimentaryTicket: function () {
      if (!this.user) return false
      if (!this.user.ticket) return false
      if (this.user.ticket.isComplimentary == true) return true
      return false
    },
    // hasRolloverTicket: function () {
    //   if (!this.user) return false
    //   if (!this.user.rolloverTicket) return false
    //   return true
    // },
    // hasDonatedTicket: function () {
    //   if (!this.user) return false
    //   if (!this.user.donatedTicket) return false
    //   return true
    // },
    // hasRefundedTicket: function () {
    //   if (!this.user) return false
    //   if (!this.user.refundRequest) return false
    //   return true
    // },
    getTicket: function() {
      if (!this.user) return null
      if (this.hasTicket) return this.user.ticket
      // if (this.hasRolloverTicket) return this.user.rolloverTicket
      // if (this.hasDonatedTicket) return this.user.donatedTicket
      // if (this.hasRefundedTicket) return this.user.refundRequest
      return null
    },
    ticketsAreSoldOut() {
      return process.env.VUE_APP_TICKETS_ARE_SOLD_OUT === 'true';
    },
    hasGuardians: function(){
      if(!this.user) return []
      if(!this.user.guardians) return []
      if(this.isObjectEmpty(this.user.guardians)) return []
      return Object.values(this.user.guardians)
    },
    hasConfirmedVaccinationNotice: function(){
      // if(!this.user) return false
      // if(!this.user.confirmVaccinationNotice) return false
      // if(this.user.confirmVaccinationNotice === true) return true
      // 2023-01-22 Ya no estamos pidiendo que confirmen la vacuna 
      if(!this.user) return false
      // if(!this.user.confirmVaccinationNotice) return true
      // if(this.user.confirmVaccinationNotice === true) return true
      return true
    },
    hasDeclinedVaccinationNotice: function(){
      if(!this.user) return false
      if(this.user.confirmVaccinationNotice === false) return true
      return false
    },
    hasCompletedPreInformation(){
      if(!this.user) return false
      let fields = ['names','surnames','birthday','country','phone','documentType','documentNumber']
      return this.checkFields(fields)
    },
    hasCompletedDataAttendee(){
      if(!this.user) return false
      let fields = ['sex','phone','country','province','city','zipCode','address']
      return this.checkFields(fields)
    },
    hasCompletedDataHealth(){
      if(!this.user) return false
      let fields = ['emergencyContact','emergencyPhone','hasMedsAllergies','hasFoodAllergies','hasOtherAllergies','hasFoodRestriction','doingMeds']
      return this.checkFields(fields)
    },
    hasCompletedDataFursona(){
      if(!this.user) return false
      let fields = ['fursonaFullname','fursonaDisplayName','fursonaSpecies','fursonaPronoun']
      return this.checkFields(fields)
    },
    hasCompletedGuardianInfo(){
      if(!this.user) return false
      if(this.userAge >= 16) return true
      if(!this.user.guardians) return false
      if(!this.hasGuardians[0]) return false
      let fields = ['adultNames','adultSurnames','adultPhone','adultDocumentType','adultDocumentNumber']
      return this.checkFieldsGuardian(fields)
    },
    hasTicketDiscount() {
      if(!this.user) return false
      if(this.user.volunteerLevel) return true
      return false
    },
    hasCompletedDataExtra(){
      if(!this.user) return false
      if(!this.hasTicket) return false
      let fields = ['fursuiter']
      if(this.user.ticket.status === 'pending' || this.user.ticket.status === 'purchasing'){
        if(this.user.previousTicket && (this.user.previousTicket.tier === 'sponsor' || this.user.previousTicket.tier === 'supersponsor')){
          fields = ['fursuiter','shirtSize']
        }
      } else {
        if(this.user.ticket.tier === 'sponsor' || this.user.ticket.tier === 'supersponsor'){
            fields = ['fursuiter','shirtSize']
        }
      }
      return this.checkFields(fields)
    },
    hasCompletedAllData: function() {
      return (
        this.hasTicket &&
        !this.hasStartedTicketUpgrade &&
        this.hasCompletedDataAttendee &&
        this.hasCompletedGuardianInfo &&
        this.hasCompletedDataHealth &&
        this.hasCompletedDataFursona &&
        this.hasCompletedDataExtra
      );
    },
    soldOutTickets: function(){
      if(process.env.VUE_APP_SOLD_OUT_TICKETS) return process.env.VUE_APP_SOLD_OUT_TICKETS.split(',')
      return []
    },
    ticketsAreUpgradesAvailable() {
      return process.env.VUE_APP_TICKETS_ARE_UPGRADES_AVAILABLE === 'true';
    },
  }
}

export default userUtils
