<template>
  <div class="calendar">
    <vue-cal
      :time="true"
      active-view="week"
      :time-from="18 * 60"
      :time-to="24 * 60"
      :selectedDate="selectedDate"
      :hideWeekdays="showDate"
      :events="calendarEvents"
      :split-days="locationsPerDay"
      :disable-views="['years', 'year', 'month','day']"
      :dragToCreateEvent="false"
      :hideTitleBar="true"
      :hideViewSelector="true"
      :specialHours="specialHours"
      :minDate="minDate"
      :maxDate="maxDate"
      sticky-split-labels
      :timeStep="30"
      :showWeekNumbers="false"
      :timeCellHeight="50"
      :overlaps-per-time-step="true"
      :watchRealTime="true"
      :dblclickToNavigate="false"
      :locale="lang"
    />
  </div>
</template>

<script>
import VueCal from "vue-cal";
import "vue-cal/dist/i18n/es.js";
export default {
  props: {
		events: {
			type: Array,
			required: false,
      default: () => []
		},
		locations: {
			type: Array,
			required: false,
      default: () => []
		},
    lang: {
      type: String,
      required: false,
      default: 'es'
    },
    showDate: {
      type: Array,
      required: false,
      default: () => [1,2,3,5,6,7]
    }
	},
  data() {
    return {
      // the min date is August 11nd 2022 and the max date is August 14th 2022
      minDate: "2022-08-11",
      maxDate: "2022-08-14",
      selectedDate: "2022-08-11",
      specialHours: {
        // 1: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 2: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 3: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 4: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 6: { from: 2 * 60, to: 2.5 * 60, class: 'final'},
        // 7: { from: 2 * 60, to: 2.5 * 60, class: 'final'}
      },
			calendarEvents: [],
      locationsPerDay: []
    };
  },
  components: {
    VueCal,
  },
  mounted() {
    this.getLocationsPerDay();
    this.getEvents();
  },
  methods: {
    getEvents() {
			let _auxEvents = []
      this.events.forEach(event => {
        let classList = []
        if(event.is_nsfw) {
          classList.push('nsfw')
        } 
        if(event.is_streamed) {
          classList.push('streamed')
        }
        if(event.is_closed) {
          classList.push('is-closed')
        }
        if(this.showDate.length == 6){
          classList.push('bigger')
        }
        let _event = {
					id: event._id,
					start: new Date(event.start_time),
					end: new Date(event.end_time),
          split: event.location,
				}
        if(this.lang === 'en') {
          _event.title = event.name_en
        } else {
          _event.title = event.name_es
        }
        if(classList.length > 0) {
          _event.class = classList.join(' ')
        }
				_auxEvents.push(_event);
			});
			this.calendarEvents = _auxEvents
    },
    getLocationsPerDay() {
			let _auxLocations = []
      this.locations.forEach(location => {
				_auxLocations.push({
					id: location._id,
					class: this.slugify(location.name),
					label: location.name,
				});
			});
			this.locationsPerDay = _auxLocations
    },
  },
	watch: {
		events: {
			handler() {
				this.getEvents();
			},
			deep: true
		},
		locations: {
			handler() {
				this.getLocationsPerDay();
			},
			deep: true
		},
    lang: {
      handler() {
        this.getEvents();
      },
      deep: true
    },
    showDate: {
      handler() {
        this.getEvents();
      },
      deep: true
    }
	}
};
</script>

