                  ></i>
                </div>
                <div class="media-content">

                  <h1 class="title is-4 is-marginless has-text-primary">{{$t('profile.preInformation.guardianInfoTitle')}}</h1>
                  <p class="is-size-7">{{$t('profile.preInformation.guardianInfoText')[0]}}</p>
                  <p class="is-size-7 has-text-info"><b>{{$t('profile.preInformation.guardianInfoText')[1]}}</b></p>
                  <p class="is-size-7 has-text-info"><b>{{$t('profile.preInformation.guardianInfoText')[2]}}</b></p>
                  </div>
                </div>
              </div>
              <div class="field is-horizontal">
                <div class="field-body">
                  <div class="field">
                    <label class="label">{{$t('profile.guardian.names')}}</label>
                    <div class="control is-expanded">
                      <ValidationProvider
                        :name="$t('profile.guardian.names')"
