                    />
                    <span class="help is-danger">{{errors[0]}}</span>
                  </ValidationProvider>
                </div>
              </div>
            </section>
            <section v-if="alreadyHasGuardian">
              <br />
              <div class="box">
                <div class="media">
                  <div class="media-left">
                    <i class="fas fa-check-circle fa-2x fa-fw has-text-success"></i>
                  </div>
                  <div class="media-content">
                    <h1 class="title is-4 is-marginless has-text-success">{{$t('profile.preInformation.thanksTitle')}}</h1>
                    <p class="is-size-7">{{$t('profile.preInformation.thanksYourInfoGuardian')}}</p>
                    <p class="is-size-7">
                      <b>{{$t('profile.preInformation.guardianInfoText')[2]}}</b>
