</template>

<script>
import userUtils from "@/mixins/userUtils";

export default {
  data() {
    return {
      isOpened: false,
      names: null,
      surnames: null,
      birthday: null,
      phone: null,
      country: null,
      documentType: null,
      documentNumber: null,
      attendeeType: null,
      adultNames: null,
      adultSurnames: null,
      adultPhone: null,
      adultDocumentType: null,
      adultDocumentNumber: null,
      alreadyCompletedDocument: true,
      isSending: false
    };
  },
  mixins: [userUtils],
  created() {
    this.sync();
  },
  methods: {
    sync: function() {
      this.names = this.getValue(this.user.names);
      this.surnames = this.getValue(this.user.surnames);
      this.birthday = this.user.birthday || null;
      this.country = this.getValue(this.user.country);
      this.phone = this.getValue(this.user.phone);
      if(this.user.documentType == undefined || this.user.documentType == undefined){
        this.alreadyCompletedDocument = false;
      } else {
        this.alreadyCompletedDocument = true;
      }
      this.documentType = this.getValue(this.user.documentType);
      this.documentNumber = this.getValue(this.user.documentNumber);
      this.attendeeType = this.getValue(this.user.attendeeType);
      if (this.user.guardians && this.hasGuardians[0]) {
        this.adultNames = this.getValue(this.hasGuardians[0].adultNames);
        this.adultSurnames = this.getValue(
          this.hasGuardians[0].adultSurnames
        );
        this.adultPhone = this.getValue(this.hasGuardians[0].adultPhone);
        this.adultDocumentType = this.getValue(
          this.hasGuardians[0].adultDocumentType
        );
        this.adultDocumentNumber = this.getValue(
          this.hasGuardians[0].adultDocumentNumber
        );
      }
    },
    async submit() {
      const result = await this.$refs.observer.validate();
      if (!result) {
        this.$swal.fire({
          icon: 'warning',
          title: this.$t('profile.alertInvalidFields.title'),
          text: this.$t('profile.alertInvalidFields.text')
        })
        return;
      }
      this.startLoading();
      if (!this.alreadyHasGuardian && this.calculatedAttendeeType === "menor") {
        let payloadGuardian = {
          adultNames: this.adultNames,
          adultSurnames: this.adultSurnames,
          adultPhone: this.adultPhone,
          adultDocumentType: this.adultDocumentType,
          adultDocumentNumber: this.adultDocumentNumber
        };
        this.$http
          .post("/guardian/add", payloadGuardian)
          .then(() => {
            this.saveUser();
          })
          .catch(err => {
            console.error(err);
             this.$swal.fire({
              icon: 'error',
              title: this.$t('profile.alertError.title'),
              text: this.$t('profile.alertError.text')
            })
          });
      } else {
        this.saveUser();
      }
    },
    saveUser: function() {
      this.isSending = true;
      let payloadUser = {
        names: this.names,
        surnames: this.surnames,
        birthday: this.birthday,
        country: this.country,
        phone: this.phone,
        documentType: this.documentType,
        documentNumber: this.documentNumber,
        attendeeType: this.calculatedAttendeeType
      };
      this.$http
        .post("/user/update", payloadUser)
        .then(() => this.updateUser())
        .then(() => {
          this.sync();
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
            this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading();
          this.isSending = false;
        });
    },
  },
  computed: {
    documentTypeOptions() {
      return Object.keys(this.$t("form.documentTypeOptions"));
    },
    countryOptions() {
      return Object.keys(this.$t("countries"));
    },
    calculatedAttendeeType: function() {
      if (!this.userAge) return null;
      if (this.userAge >= 18) return "mayor";
      if (this.userAge < 18 && this.userAge >= 16) return "medio";
      if (this.userAge < 16) return "menor";
      return null;
    },
    alreadyHasGuardian: function() {
      if (this.user.guardians && this.hasGuardians[0]) return true;
      return false;
    },
    arffDateStart: function() {
      return new Date(process.env.VUE_APP_ARFF_DATE_START)
    },
    userAge: function() {
      if (!this.birthday) return null;
      // check if it is a valid date string YYYY-MM-DD
      if (this.birthday.length !== 10) return null;
      let birthDate = new Date(this.birthday);
      let age = this.arffDateStart.getFullYear() - birthDate.getFullYear();
      let m = this.arffDateStart.getMonth() - birthDate.getMonth();
      if (m < 0 ||(m === 0 && this.arffDateStart.getDate() < birthDate.getDate())) {
        age--;
      }
      return age;
    }
  }
};
</script>