import store from '@/store';

const baseUtils = {
  computed: {
    apiUrl: function(){
      return process.env.VUE_APP_API_URL
    },
    isSpanish: function () {
			return !window.location.pathname.includes('/en/');
		},
    ticketsPurchaseAvailable() {
      return process.env.VUE_APP_TICKETS_PURCHASE_AVAILABLE === 'true';
    },
    ticketsEnableWaitlist() {
      return process.env.VUE_APP_ENABLE_TICKET_WAITLIST === 'true';
    },
    ticketsAreSoldOut() {
      return process.env.VUE_APP_TICKETS_ARE_SOLD_OUT === 'true';
    },
    ticketsAreUpgradesAvailable() {
      return process.env.VUE_APP_TICKETS_ARE_UPGRADES_AVAILABLE === 'true';
    },
    ticketsShowFoundersNotification() {
      return process.env.VUE_APP_TICKETS_FOUNDERS_NOTIFICATION === 'true';
    },
    ticketsShowAnnoucementNotification() {
      return process.env.VUE_APP_TICKETS_ANNOUCEMENT_NOTIFICATION === 'true';
    },
    roomPricesAvailable() {
      return process.env.VUE_APP_ROOM_PRICES_AVAILABLE === 'true';
    },
    ticketsAnnoucementTitle() {
      if(!this.isSpanish) return process.env.VUE_APP_TICKETS_ANNOUCEMENT_EN_TITLE;
      return process.env.VUE_APP_TICKETS_ANNOUCEMENT_ES_TITLE;
    },
    ticketsAnnoucementDescription() {
      if(!this.isSpanish) return process.env.VUE_APP_TICKETS_ANNOUCEMENT_EN_DESCRIPTION;
      return process.env.VUE_APP_TICKETS_ANNOUCEMENT_ES_DESCRIPTION
    },
  },
  methods: {
    startLoading() {
      store.commit('setLoading', true);
    },
    stopLoading() {
      store.commit('setLoading', false);
    },
    makeUrl: function(url,redirect){
      let theUrl = null
      if(!this.isSpanish) {
        theUrl = `/en${url}`
        if(redirect) theUrl += `?redirect=/en${redirect}`
      } else {
        theUrl = `${url}`
        if(redirect) theUrl += `?redirect=${redirect}`
      }
      return theUrl
    },
    getValue(value) {
      if (value === undefined) return null;
      if (value === null) return null;
      return value;
    },
    isOptional(value) {
      if (value === null || value === '') {
        return null;
      }
      if (typeof value !== 'undefined' && value.length === 0) {
        return [];
      } return value;
    },
    isInputEmpty(value) {
      if (value === null || value === '') {
        return true;
      }
      if (typeof value !== 'undefined' && value.length === 0) {
        return true;
      } return false;
    },
    isObject(val) {
      return val !== null && typeof val === 'object' && Array.isArray(val) === false;
    },
    isObjectEmpty(obj) {
      for(var key in obj) {
          // eslint-disable-next-line no-prototype-builtins
          if(obj.hasOwnProperty(key))
              return false;
      }
      return true;
    },
    hasValue(val) {
      if (val === undefined || val === null) {
        return false
      }
      return true
    },
    slugify(text) {
      return text.toString().toLowerCase()
        .replace(/\s+/g, '-') // Replace spaces with -
        .replace(/[^\w-]+/g, '') // Remove all non-word chars
        .replace(/--+/g, '-') // Replace multiple - with single -
        .replace(/^-+/, '') // Trim - from start of text
        .replace(/-+$/, ''); // Trim - from end of text
    },
  }
}

export default baseUtils
