const needsLogin = (component, id) => {
  http.get('/user/loggedIn').then( response => {
  if(response.data.loggedIn){
    return http.get('/user')
  }
    throw Error ('Needs to be logged in')
  })
  .then(response => {
    store.commit('updateUser', response.data)
    new Vue({
      i18n,
      store,
      http,
      render: h => h(component),
    }).$mount(`#${id}`)
    return
  })
  .catch(err => {
    console.log(err)
    const redirectUrl = window.location.pathname
    window.location.replace(`/signin?redirect=${redirectUrl}`);
  })  
}

