<template>
  <section>
    <div class="box">
      <div class="is-flex is-flex-direction-row is-justify-content-space-between is-clickable" @click="isOpened = !isOpened">
        <p class="is-size-4 is-700">
          {{$t('profile.guardians.addNew')}}
        </p>
        <p class="is-size-4">
          <i class="fas" :class="isOpened ? 'fa-angle-up' : 'fa-angle-down'"></i>
        </p>
      </div>
      <fieldset v-if="isOpened">
        <br>
        <ValidationObserver ref="observer" v-slot="{ invalid }">
            <div class="field is-horizontal">
              <div class="field-body">
                <div class="field">
                  <label class="label">{{$t('profile.guardian.names')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.guardian.names')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="adultNames"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.guardian.surnames')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.guardian.surnames')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="adultSurnames"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
              </div>
            </div>
            <div class="field is-horizontal">
              <div class="field-body">
                <div class="field">
                  <label class="label">{{$t('profile.guardian.documentType')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.guardian.documentType')"
                      :rules="{required: true}"
                      v-slot="{errors}"
                    >
                      <div class="select is-fullwidth">
                        <select v-model.lazy="adultDocumentType">
                          <option :value="null">{{$t('form.selectOne')}}</option>
                          <option
                            v-for="key in documentTypeOptions"
                            :key="key"
                            :value="key"
                          >{{$t('form.documentTypeOptions')[key]}}</option>
                        </select>
                      </div>
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.guardian.documentNumber')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.guardian.documentNumber')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="adultDocumentNumber"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
              </div>
            </div>
            <div class="field">
              <label class="label">{{$t('profile.guardian.phone')}}</label>
              <div class="control is-expanded">
                <ValidationProvider
                  :name="$t('profile.guardian.phone')"
                  :rules="{required: true}"
                  v-slot="{errors, classes}"
                >
                  <input
                    type="text"
                    v-model="adultPhone"
                    class="input"
                    :class="classes"
                    @keyup.enter="submit"
                    :placeholder="$t('form.required')"
                  />
                  <span class="help is-danger">{{errors[0]}}</span>
                </ValidationProvider>
              </div>
            </div>
          <br />
          <div class="field">
            <div class="control">
              <div class="buttons is-right">
                <button
                  class="button is-primary"
                  :disabled="invalid || isSending"
                  :class="{'is-loading': isSending}"
                  @click="submit"
                >
                  <span class="spaced">
                    <i class="fas fa-save"></i>
                    &nbsp;{{$t('form.save')}}
                  </span>
                </button>
              </div>
            </div>
          </div>
        </ValidationObserver>
    </fieldset>
    </div>
  </section>
</template>

<script>
import userUtils from "@/mixins/userUtils";

export default {
  data() {
    return {
      isOpened: false,
      isSending: false,
      //data
      adultNames: null,
      adultSurnames: null,
      adultDocumentType: null,
      adultDocumentNumber: null,
      adultPhone: null
    };
  },
  created() {
    
    },
  mixins: [userUtils],
  methods: {
    async submit() {
      const result = await this.$refs.observer.validate();
      if (!result) {
        this.$swal.fire({
          icon: 'warning',
          title: this.$t('profile.alertInvalidFields.title'),
          text: this.$t('profile.alertInvalidFields.text')
        })
        return;
      }
      this.startLoading();
      this.isSending = true;
      const payload = {
        adultNames: this.adultNames,
        adultSurnames: this.adultSurnames,
        adultDocumentType: this.adultDocumentType,
        adultDocumentNumber: this.adultDocumentNumber,
        adultPhone: this.adultPhone
      }
      this.$http
        .post("/guardian/add", payload)
        .then(() => this.updateUser())
        .then(() => {
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading();
          this.isSending = false;
        });
    }
  },
  computed: {
    documentTypeOptions() {
      return Object.keys(this.$t("form.documentTypeOptions"));
    },
    countryOptions() {
      return Object.keys(this.$t("countries"));
    },
  }
};
</script>