                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.participant.surnames')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.participant.surnames')"
                      :rules="{required: true}"
