                    >
                      <input
                        type="text"
                        v-model="surnames"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                        :disabled="wasSurnamesAdded"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.participant.email')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.participant.email')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="email"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                        disabled
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
              </div>
            </div>
            <div class="field is-horizontal">
              <div class="field-body">
                <div class="field">
                  <label class="label">{{$t('profile.participant.country')}}</label>
                  <div class="control">
                    <ValidationProvider
                      :name="$t('profile.participant.country')"
                      :rules="{required: true}"
                      v-slot="{errors}"
                    >
                      <div class="select is-fullwidth">
                        <select v-model.lazy="country" :disabled="wasCountryAdded">
                          <option :value="null">{{$t('form.selectOne')}}</option>
                          <option
                            v-for="key in countryOptions"
                            :key="key"
                            :value="key"
                          >{{$t('countries')[key]}}</option>
                        </select>
                      </div>
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.participant.province')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.participant.province')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="province"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
              </div>
            </div>
            <div class="field is-horizontal">
              <div class="field-body">
                <div class="field">
                  <label class="label">{{$t('profile.participant.city')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.participant.city')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="city"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.participant.zipCode')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.participant.zipCode')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="zipCode"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.participant.address')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.participant.address')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="address"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
              </div>
            </div>
            <div class="field is-horizontal">
              <div class="field-body">
                <div class="field">
                  <label class="label">{{$t('profile.participant.sex')}}</label>
                  <div class="control">
                    <ValidationProvider
                      :name="$t('profile.participant.sex')"
                      :rules="{required: true}"
                      v-slot="{errors}"
                    >
                      <div class="select is-fullwidth">
                        <select v-model.lazy="sex">
                          <option :value="null">{{$t('form.selectOne')}}</option>
                          <option
                            v-for="key in sexOptions"
                            :key="key"
   
                            :value="key"
                          >{{$t('form.sexOptions')[key]}}</option>
                        </select>
                      </div>
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.participant.documentType')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.participant.documentType')"
                      :rules="{required: true}"
                      v-slot="{errors}"
                    >
                      <div class="select is-fullwidth">
                        <select v-model.lazy="documentType" :disabled="wasDocumentTypeAdded">
                          <option :value="null">{{$t('form.selectOne')}}</option>
                          <option
                            v-for="key in documentTypeOptions"
                            :key="key"
                            :value="key"
                          >{{$t('form.documentTypeOptions')[key]}}</option>
                        </select>
                      </div>
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.participant.documentNumber')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.participant.documentNumber')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="documentNumber"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                        :disabled="wasDocumentNumberAdded"
                      />
                      <span class="help is-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </div>
              </div>
            </div>
            <div class="field is-horizontal">
              <div class="field-body">
                <div class="field">
                  <label class="label">{{$t('profile.participant.birthday')}} *</label>
                  <div class="control">
                    <ValidationProvider
                      :name="$t('profile.participant.birthday')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                    <input
                        type="text"
                        v-model="birthday"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        v-maska="'####-##-##'"
                        :placeholder="`YYYY-MM-DD (${$t('form.required')})`"
                        :disabled="wasBirthdayAdded"
                      />
                      <span class="help is-danger">{{errors[0]}}</span>
                    </ValidationProvider>
                  </div>
                </div>
                <div class="field">
                  <label class="label">{{$t('profile.participant.phone')}}</label>
                  <div class="control is-expanded">
                    <ValidationProvider
                      :name="$t('profile.participant.phone')"
                      :rules="{required: true}"
                      v-slot="{errors, classes}"
                    >
                      <input
                        type="text"
                        v-model="phone"
                        class="input"
                        :class="classes"
                        @keyup.enter="submit"
                        :placeholder="$t('form.required')"
                      />
                      <span class="help is-danger">{{errors[0]}}</span>
                    </ValidationProvider>
                  </div>
                </div>
              </div>
            </div>
          <div class="field">
            <div class="control">
              <div class="buttons is-right">
                <button
                  class="button is-primary"
                  :disabled="invalid || isSending"
                  :class="{'is-loading': isSending}"
                  @click="submit"
                >
                  <span class="spaced">
                    <i class="fas fa-save"></i>
                    &nbsp;{{$t('form.save')}}
                  </span>
                </button>
              </div>
            </div>
          </div>
          </ValidationObserver>
        </fieldset>
      </div>
  </section>
</template>

<script>
import userUtils from "@/mixins/userUtils";

export default {
  data() {
    return {
      isOpened: false,
      isSending: false,
      //data
      names: null,
      surnames: null,
      birthday: null,
      documentType: null,
      documentNumber: null,
      sex: null,
      email: null,
      phone: null,
      country: null,
      province: null,
      city: null,
      zipCode: null,
      address: null,
      wasNamesAdded: false,
      wasSurnamesAdded: false,
      wasBirthdayAdded: false,
      wasCountryAdded: false,
      wasDocumentTypeAdded: false,
      wasDocumentNumberAdded: false
    };
  },
  created() {
    this.sync();
  },
  mixins: [userUtils],
  methods: {
    sync: function() {
      this.names = this.getValue(this.user.names);
      this.wasNamesAdded = this.getValue(this.user.names)
        ? true
        : false;
      this.surnames = this.getValue(this.user.surnames);
      this.wasSurnamesAdded = this.getValue(this.user.surnames)
        ? true
        : false;
      this.birthday = this.user.birthday || null;
      this.wasBirthdayAdded = this.getValue(this.user.birthday)
        ? true
        : false;
      this.documentType = this.getValue(this.user.documentType);
      this.wasDocumentTypeAdded = this.getValue(this.user.documentType)
        ? true
        : false;
      this.documentNumber = this.getValue(this.user.documentNumber);
      this.wasDocumentNumberAdded = this.getValue(this.user.documentNumber)
        ? true
        : false;
      this.attendeeType = this.getValue(this.user.attendeeType);
      this.sex = this.getValue(this.user.sex);
      this.email = this.getValue(this.user.email);
      this.phone = this.getValue(this.user.phone);
      this.country = this.getValue(this.user.country);
      this.wasCountryAdded = this.getValue(this.user.country)
        ? true
        : false;
      this.province = this.getValue(this.user.province);
      this.city = this.getValue(this.user.city);
      this.zipCode = this.getValue(this.user.zipCode);
      this.address = this.getValue(this.user.address);
    },
    async submit() {
      const result = await this.$refs.observer.validate();
      if (!result) {
        this.$swal.fire({
          icon: 'warning',
          title: this.$t('profile.alertInvalidFields.title'),
          text: this.$t('profile.alertInvalidFields.text')
        })
        return;
      }
      this.startLoading();
      this.isSending = true;
      let payload = {
        sex: this.sex,
        phone: this.phone,
        country: this.country,
        province: this.province,
        city: this.city,
        zipCode: this.zipCode,
        address: this.address,
      };
      if(
        !this.wasNamesAdded &&
        !this.wasSurnamesAdded &&
        !this.wasBirthdayAdded &&
        !this.wasDocumentTypeAdded &&
        !this.wasDocumentNumberAdded &&
        !this.wasCountryAdded
      ){
        payload.names = this.names
        payload.surnames = this.surnames
        payload.birthday = this.birthday
        payload.documentType = this.documentType
        payload.documentNumber = this.documentNumber
        payload.country = this.country
      }
      this.$http
        .post("/user/update", payload)
        .then(() => this.updateUser())
        .then(() => this.sync())
        .then(() => {
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading();
          this.isSending = false;
        });
    }
  },
  computed: {
    documentTypeOptions() {
      return Object.keys(this.$t("form.documentTypeOptions"));
    },
    countryOptions() {
      return Object.keys(this.$t("countries"));
    },
    sexOptions() {
      return Object.keys(this.$t('form.sexOptions'));
    }
  }
};
</script>