<template>
  <div class="cardContainer scene--card">
    <div class="cardElement" @click="flipCard($event)">
      <div class="card__face card__face--front">
        <div class="cardFrontDecoration" :class="{'cursed': challenge.cursed}" :style="hasBackground ? `background-image: url('${ isSpanish ? challenge.background.es : challenge.background.en }'); background-size: 100% 100%;` : null">
          <p class="categoryContainer" :class="hasBackground  ? `helper` : null">
          ~ {{ challenge.category }} ~ 
          </p>
          <div class="pointsContainer">
            <!-- <p class="pts" v-if="challenge.cursed && !challenge.solves.includes(user._id)">{{$t('ctf.upTo')}}</p> -->
            <p class="pts has-text-warning" v-if="challenge.cursed && !challenge.solves.includes(user._id)"><i class="fas fa-gem"></i> {{$t('ctf.special')}}</p>
            <p class="ptsNumber" v-if="!challenge.solves.includes(user._id)"><span v-if="challenge.cursed">±</span>{{Math.floor(challenge.points * Math.pow(0.95, challenge.solves.length)) > 1 ? nFormatter(Math.floor(challenge.points * Math.pow(0.95, challenge.solves.length))) : 1}}</p>
            <p class="ptsHecho" v-else>{{ isSpanish ? 'ENCONTRADA' : 'FOUND'}}</p>
            <p class="pts" v-if="!challenge.solves.includes(user._id)">{{$t('ctf.miles')}}</p>
          </div>
          <p class="titleContainer" :class="hasBackground  ? `helper` : null">
            {{ isSpanish ? challenge.title.es : challenge.title.en }}
          </p>
        </div>
      </div>
      <div class="card__face card__face--back">
        <div class="cardBackDecoration">
            <p class="description" v-html="isSpanish ? challenge.text.es : challenge.text.en"></p>
            <div class="pointsContainer">
              <p class="ptsNumber">{{challenge.solves.length}}</p>
              <p class="pts is-uppercase">{{ isSpanish ? 'capturas' : 'captures'}}</p>
            </div>
            <img :src="`https://api.furfiesta.com/ctf/avatar/${challenge.solves[0]}`" v-if="challenge.solves && challenge.solves.length > 0" :key="`challenge-${challenge._id}-solver-${challenge.solves[0]}`" class="image the-solver" />  
            <div v-else></div>
            <div class="solvers-container" v-if="challenge.solves && challenge.solves.length > 1">
              <img :src="`https://api.furfiesta.com/ctf/avatar/${solvers}`" v-for="(solvers,j) in challenge.solves.slice(1,6)" :key="`challenge-${challenge._id}-solver-${j}`" class="image" />  
              </div>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['challenge','user'],
  computed: {
    hasBackground: function(){
      return this.challenge.background != undefined && this.challenge.background.es != '' && this.challenge.background.en != ''
    }
  },
  methods: {
    flipCard: function(e) {
			// let theElement = this.closestElement(e.target, 'cardElement').classList.toggle('is-flipped')
			this.closestElement(e.target, 'cardElement').classList.toggle('is-flipped')
			//console.log(theElement)
			//theElement.classList.toggle('is-flipped')
		},
    hasClass: function(elem, cls) {
      var str = " " + elem.className + " ";
      var testCls = " " + cls + " ";
      return(str.indexOf(testCls) != -1) ;
		},
		closestElement: function(el, cls) {
			while (el  && el !== document) {
        if (this.hasClass(el, cls)) return el;
        el = el.parentNode;
			}
			return null;
		},
    nFormatter: function (num, digits) {
      const lookup = [
        { value: 1, symbol: "" },
        { value: 1e3, symbol: "k" },
        { value: 1e6, symbol: "M" },
        { value: 1e9, symbol: "G" },
        { value: 1e12, symbol: "T" },
        { value: 1e15, symbol: "P" },
        { value: 1e18, symbol: "E" }
      ];
      const rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
      var item = lookup.slice().reverse().find(function(item) {
        return num >= item.value;
      });
      return item ? (num / item.value).toFixed(digits).replace(rx, "$1") + item.symbol : "0";
    },
  }
}
</script>

