<template>
  <section class="mb-4">
    <div class="py-2 box mb-0 has-background-success is-size-7" v-if="hasCompletedGuardianInfo">
      <p class="has-text-white">
        <i class="fas fa-check fa-fw"></i>&nbsp;{{$t('profile.guardians.completed')}}
      </p>
    </div>
    <div class="py-2 box mb-0 has-background-warning is-size-7" v-else>
      <p class="has-text-black">
        <i class="fas fa-arrow-down fa-fw"></i>&nbsp;{{$t('profile.guardians.notCompleted')}}
      </p>
    </div>
    <div class="box">
      <div class="is-flex is-flex-direction-row is-justify-content-space-between is-clickable" @click="isOpened = !isOpened">
        <p class="is-size-4 is-700">
          {{$t('profile.guardians.title')}}<span v-if="hasCompletedGuardianInfo">&nbsp;&nbsp;<i class="fas fa-check-circle has-text-success fa-fw"></i></span>
        </p>
        <p class="is-size-4">
          <i class="fas" :class="isOpened ? 'fa-angle-up' : 'fa-angle-down'"></i>
        </p>
      </div>


      <div class="box my-5" v-show="isOpened">
        <div class="media">
          <div class="media-left">
            <i class="fas fa-exclamation-triangle fa-2x fa-fw has-text-info"></i>
          </div>
          <div class="media-content">
            <h1 class="title is-4 is-marginless has-text-link">{{$t('profile.important')}}</h1>
            <p class="is-size-7" v-for="(p,index) in $t('profile.guardians.notice')" :key="index">
              {{p}}
            </p>
          </div>
        </div>
      </div>
      <div class="box my-5" v-show="isOpened">
        <div class="media">
          <div class="media-left">
            <i class="fas fa-question-circle fa-2x fa-fw has-text-info"></i>
          </div>
          <div class="media-content">
            <h1 class="title is-4 is-marginless has-text-link">{{$t('profile.guardians.noticeTwo.title')}}</h1>
            <p class="is-size-7" v-for="(p,index) in $t('profile.guardians.noticeTwo.text')" :key="index">
              {{p}}
            </p>
          </div>
        </div>
      </div>
      <div class="columns" v-show="isOpened">
        <div class="column" v-for="(guardian, index) in hasGuardians" :key="index">
          <DataGuardian :canDelete="canDelete" :guardian="guardian"/>
        </div>
      </div>
      <NewGuardian  v-if="!canDelete && isOpened"/>
    </div>
  </section>
