<script>
import DataGuardian from "@/components/profile/DataGuardian.vue";
import NewGuardian from "@/components/profile/NewGuardian.vue";
import userUtils from "@/mixins/userUtils";

export default {
  components: {
    DataGuardian,
    NewGuardian
  },
  mixins: [userUtils],
  data() {
    return {
      isOpened: false,
      isSending: false
    };
  },
  created() {
    this.sync();
  },
  methods: {
    sync: function() {},
  },
  computed:{
    canDelete: function(){
      if(!this.user.guardians) return false
      if(this.hasGuardians.length > 1) return true
      return false;
    }
  }
};
</script>