<template>
  <section>
    <div class="box">
        <h1 class="title is-4 is-marginless">Guardian</h1>
        <p class="is-size-7">{{$t('profile.guardian.description')}}</p>
        <br>
        <div class="field is-horizontal">
          <div class="field-body">
            <div class="field">
              <label class="label">{{$t('profile.guardian.names')}}</label>
              <div class="control">{{guardian.adultNames}}</div>
            </div>
            <div class="field">
              <label class="label">{{$t('profile.guardian.surnames')}}</label>
              <div class="control">{{guardian.adultSurnames}}</div>
            </div>
          </div>
        </div>
        <div class="field is-horizontal">
          <div class="field-body">
            <div class="field">
              <label class="label">{{$t('profile.guardian.documentType')}} *</label>
              <div class="control">{{$t('form.documentTypeOptions')[guardian.adultDocumentType]}}</div>
            </div>
            <div class="field">
              <label class="label">{{$t('profile.guardian.documentNumber')}}*</label>
              <div class="control">{{guardian.adultDocumentNumber}}</div>
            </div>
          </div>
        </div>
        <div class="field">
          <label class="label">{{$t('profile.guardian.phone')}} *</label>
          <div class="control">{{guardian.adultPhone}}</div>
        </div>
        <div class="field" v-if="canDelete">
            <div class="control">
              <div class="buttons is-centered">
                <button
                  class="button is-danger"
                  :disabled="isSending"
                  :class="{'is-loading': isSending}"
                  @click="submit"
                >
                  <span class="spaced">
                    <i class="fas fa-trash"></i>
                    &nbsp;{{$t('profile.guardian.deleteGuardian')}}
                  </span>
                </button>
              </div>
              <p class="is-size-7 has-text-centered">{{$t('profile.guardian.cantDelete')}}</p>
            </div>
          </div>
    </div>
  </section>
</template>

<script>
import userUtils from "@/mixins/userUtils";

export default {
  props: {
    canDelete: {
      type: Boolean,
      default: false,
    },
    guardian: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      openedSection: false,
      isSending: false
    };
  },
  created() {
    this.sync();
  },
  mixins: [userUtils],
  methods: {
    sync: function() {},
    async submit() {
      this.startLoading();
      this.isSending = true;
      const payload = {
        id: this.guardian.id
      }
      this.$http
        .post("/guardian/delete", payload)
        .then(() => this.updateUser())
        .then(() => this.sync())
        .then(() => {
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading()
          this.isSending = false;
        });
    }
  },
  computed: {
    documentTypeOptions() {
      return Object.keys(this.$t("form.documentTypeOptions"));
    }
  }
};
</script>