<template>
  <section>
    <div class="has-text-centered mb-5">
        <b-field :label="$t('calendar.calendarLanguage')" centerd>
          <b-switch
            v-model="lang"
            passive-type='is-primary'
            type='is-primary'
            size='is-medium'
            true-value="en"
            false-value="es"
            >
            {{ lang === 'es' ? $t('calendar.calendarLanguage_es') : $t('calendar.calendarLanguage_en') }}
          </b-switch>
        </b-field>
      </div>
    <CalendarPrevia :events="allEvents" :lang="lang" :locations="locations" :showDate="showDate"/>
    <br>    
    <h1 class="title is-3 has-text-centered is-uppercase">{{$t('calendar.listSchedule')}}</h1>
    <div class="event-container is-flex is-flex-direction-row is-align-items-strech" v-for="event in eventsByLocation" :key="`event-${event._id}`">
      <div class="event-time is-flex is-flex-direction-column is-justify-content-center" style="width: 100px;">
        <p class="is-size-7 is-uppercase">{{getShortDay(event.start_time)}}</p>
        <p class="is-size-4"><b>{{getTime(event.start_time)}}</b></p>
        <p class="is-size-6">- <b>{{getTime(event.end_time)}}</b></p>
      </div>
      <div class="event-description-container">
        <div class="event-location">
          <div class="map-reference-circle">{{getLocationMapReferenceFromId(event.location)}}</div>
          <p>{{getLocationNameFromId(event.location)}}</p>
        </div>
        <div class="event-description">
          <div class="is-flex is-flex-direction-row is-align-items-center mb-3">
            <div>
              <span class="tag is-danger is-800 mr-2 is-size-6" v-show="event.is_nsfw">+18</span> <span class="tag is-info is-800 mr-2 is-size-6" v-show="event.is_streamed"><i class="fas fa-video"></i></span>
            </div>
            <div class="" style="border-left: 5px solid #16425B; padding-left: 6px; width: 100%">
              <p class="is-size-4 is-700" style="line-height:1.5rem;">{{event.name_es}}</p>
              <p class="has-text-primary is-italics is-size-7">{{event.name_en}}</p>
            </div>
          </div>
          <p><span class="has-text-info is-size-5 has-text-weight-bold">{{event.speakers}}</span></p>
          <p class="is-size-6 is-400 mb-2">[🇦🇷]  {{event.description_es}}</p>
          <p class="is-size-7 is-400">[🇺🇸] <span class="is-italic">{{event.description_en}}</span></p>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import CalendarPrevia from '@/components/cronograma/CalendarPrevia.vue' 
import moment from 'moment'

export default {
  data(){
    return {
      isLoading: false,
      componentKey: 0,
      locations: [],
      allEvents: [],
      events: [],
      closedTimes: [],
      lang: 'es',
      showDate: [1,2,3,5,6,7],
      showDateSelected: 0,
      selectedLocation: null,
      filterDate: null
    }
  },
  components: {
    CalendarPrevia
  },
   mounted() {
    this.getAll()
  },
  computed: {
    eventsByLocation() {
      const eventsFilteredNoPrevia = this.events.filter(e => (moment(e.start_time).format('YYYY-MM-DD') == '2022-08-11' || moment(e.start_time).subtract(1,'hour').format('YYYY-MM-DD') == '2022-08-11'))
      const _eventsFilteredByDate = eventsFilteredNoPrevia.filter(event => {
        if(this.filterDate == null) {
          return true
        }
        return moment(event.start_time).subtract(3,'hour').format('YYYY-MM-DD') == this.filterDate
      })
      if(this.selectedLocation != null) {
        return _eventsFilteredByDate.filter(event => (event.location == this.selectedLocation))
      } else {
        return _eventsFilteredByDate
      }
    },
  },
  methods: {
     getAll() {
      this.startLoading();
      // dummy data
      Promise.all([
        this.$http.get('/conops/schedule/locations'),
        this.$http.get('/conops/schedule/events')
      ])
      .then(response => {
        this.locations = response[0].data.locations
        // sort event by start_time ASC
        // remove 1 minute from endtime to avoid overlap with starttime
        
        this.allEvents = response[1].data.events.filter(e => (moment(e.start_time).format('YYYY-MM-DD') == '2022-08-11' || moment(e.start_time).subtract(1,'minute').format('YYYY-MM-DD') != '2022-08-11'))
        this.events = response[1].data.events.filter(e => !e.is_closed).sort((a, b) => {
          return moment(a.start_time).diff(moment(b.start_time))
        })
        // filter events from day 2022-08-11
        this.closedTimes = response[1].data.events.filter(e => e.is_closed).sort((a, b) => {
          return moment(a.start_time).diff(moment(b.start_time))
        })
      })
      .catch(err => {
        console.log(err)
        this.$buefy.snackbar.open({
          message: 'Error getting data',
        })
      })
      .finally( () => {
        this.stopLoading();
      })
    },
    getFormatedDate(date){
    // format date to YYYY-mm-dd HH:MM:SS
    return moment(date).format('DD/MM/YYYY')
    },
    getTime(date){
      // format date to YYYY-mm-dd HH:MM:SS
      return moment(date).format('HH:mm')
    },
    getDay(date){
      // format date to YYYY-mm-dd HH:MM:SS
      return moment(date).format('dddd')
    },
    getShortDay(date){
      // format date to YYYY-mm-dd HH:MM:SS
      return moment(date).format('ddd DD/MM')
    },
    getLocationNameFromId(locationId) {
      const location = this.locations.find(l => l._id == locationId)
      return location ? location.name : '???'
    },
    getLocationMapReferenceFromId(locationId) {
      const location = this.locations.find(l => l._id == locationId)
      return location ? location.map_reference : '???'
    },
  }
}
</script>

