<script>
// import CuteBox from '../components/ctf/CuteBox.vue'
import Leaderboard from '../components/ctf/Leaderboard.vue'
import Challenge from '../components/ctf/Challenge.vue'
// import Glitch from '../components/ctf/Glitch.vue'
import userUtils from '../mixins/userUtils'

export default {
	data() {
		return {
			givemedate: new Date() / 1,
			//-----------
			acceptedTerms: false,
			fetchDone: false,
			authenticated: false,
			ctfpoints: 0,
			arrChallenges: [],
			inputFlag: null,
			sendingFlag: false,
			idIntervalChallenges: null,
			idIntervalScore: null,
			categoriesSelected: []
		}
	},
	mixins: [userUtils],
	components: {
		// CuteBox,
		Leaderboard,
		Challenge,
		// Glitch
	},
	mounted: function () {
		// REQUIRED
		Promise.all([
			this.$http.get('/ctf/score'),
			this.$http.get('/ctf/challenges')
		])
		.then(responses => {
			this.ctfpoints = responses[0].data
			this.arrChallenges = responses[1].data 
			this.idIntervalScore = setInterval(this.refreshScore, 15000);
			this.idIntervalChallenges = setInterval(this.refreshChallenges, 15000);
		})
		.then( () => {
			this.toggleAllCategories();
		})
		.catch((errUser) => {
			console.error(errUser)
		})
		.finally(() => {
			this.fetchDone = true
		})
	},
	computed: {
		codeName: function(){
			return this.userNames.replace(/\s/g, '-').toLowerCase()
		},
		sortedChallenges: function() {
			if(!this.arrChallenges.length) return []
			let arr = this.arrChallenges.map(c => {
				let aux = Object.assign({},c)
				if(c.solves.includes(this.user._id)){
					aux.actualPoints = 0
				} else if (c.cursed) {
					aux.actualPoints = c.points
				} else {
					aux.actualPoints = Math.floor(c.points * Math.pow(0.75, c.solves.length)) > 1 ? Math.floor(c.points * Math.pow(0.75, c.solves.length)) : 1
				}
				return aux
			})
			return arr.sort( (a,b) => b.actualPoints - a.actualPoints).filter( a => this.categoriesSelected.includes(a.category))
		},
		categoryList: function(){
			if(!this.arrChallenges.length) return []
			let onlyUniques = function(value, index, self){
		return self.indexOf(value) === index        
			}
			return this.arrChallenges.map(c => c.category).filter(onlyUniques)
		}
	},
	beforeDestroy: function(){
		clearInterval(this.idIntervalChallenges);
		clearInterval(this.idIntervalScore);
	},
	methods: {
		// acceptTerms: function(){
		// 	// let ran = Math.floor(Math.random() * 3)
		// 	let url = 'https://new.furfiesta.com/wp-content/uploads/2021/05/87dc477ba40aafde873221345902142e5439f5f9.flac'
		// 	// if(ran > 1){
		// 		// url = 'https://new.furfiesta.com/wp-content/uploads/2021/10/8fb8015cce881dedfb0be942e2c60b670f1bdff4.mp3'
		// 	// }
		// 	let audioThingy = new Audio(url);
		// 	audioThingy.volume = 0.7;
		// 	audioThingy.play();
		// 	this.acceptedTerms = true
		// },
		// nFormatter: function (num, digits) {
		// nFormatter: function (num) {
		// 	return Math.floor(num)
			// const lookup = [
			// 	{ value: 1, symbol: "" },
			// 	{ value: 1e3, symbol: "k" },
			// 	{ value: 1e6, symbol: "M" },
			// 	{ value: 1e9, symbol: "G" },
			// 	{ value: 1e12, symbol: "T" },
			// 	{ value: 1e15, symbol: "P" },
			// 	{ value: 1e18, symbol: "E" }
			// ];
			// let aux = 0
			// if(num < 0) {
			// 	aux = 1
			// 	num = -num
			// }
			// const rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
			// var item = lookup.slice().reverse().find(function(item) {
			// 	return num >= item.value;
			// });
			// return (aux ? "-" : "") + (num / item.value).toFixed(digits).replace(rx, "$1") + item.symbol;
		// },
		restartAll: function () {
			// From here, add whatever you want
			// ---
			this.acceptedTerms = false
			this.fetchDone = false
			this.authenticated = false
			this.ctfpoints = 0
			this.arrChallenges = []
			this.inputFlag = null
			this.sendingFlag = false
			this.idIntervalChallenges = null
			this.idIntervalScore = null
			this.categoriesSelected = null
		},
		refreshChallenges: function(){
			console.log('[CTF] [Challenges] Refreshing challenges...')
			this.$http.get('/ctf/challenges')
			.then( res => {
				this.arrChallenges = res.data
			})
			.catch((err) => {
				console.error(err)
			});
		},
		refreshScore: function(){
			console.log('[CTF] [Score] Refreshing score...')
			this.$http.get('/ctf/score')
			.then( res => {
				this.ctfpoints = res.data
			})
			.catch((err) => {
				console.error(err)
				clearInterval(this.idIntervalChallenges);
				clearInterval(this.idIntervalScore);
				this.restartAll()
			});
		},
		sendFlag: function(){
			if(!this.inputFlag) return
			this.sendingFlag = true
			this.$http.post('/ctf/solve', {
				flag: this.inputFlag
			}, {
				withCredentials: true
			})
				.then((res) => {
					this.inputFlag = null;
					this.refreshChallenges();
					// setTimeout(this.refreshLeaderboard, 1500);
					this.refreshScore();
					if(res.data.points > 0){
						
						(new Audio('https://new.furfiesta.com/wp-content/uploads/2021/04/78cca3ae453d88736b04287a3eb6ab080fbbf39e.mp3')).play();
						this.$swal.fire({
							icon: 'success',
							title: this.$t('ctf.success.title'),
							text: this.$t('ctf.success.text', {points: res.data.points, ctfpoints: res.data.ctfpoints})
						})
					} else {
						(new Audio('http://new.furfiesta.com/wp-content/uploads/2021/10/b75f442c5f61530b16255b20df82c194dabf80b0.mp3')).play();
						this.$swal.fire({
							icon: 'warning',
							title: this.$t('ctf.warning.title'),
							text: this.$t('ctf.warning.text', {points: -res.data.points, ctfpoints: res.data.ctfpoints})
						})
					}
				})
				.catch((err) => {
					(new Audio('https://new.furfiesta.com/wp-content/uploads/2021/04/9d28b01d69e953a02ad5b263a57b09bfdbdb3ad2.mp3')).play();
					if(err.response.data.message == 'you have already solved this challenge'){
						this.$swal.fire({
							icon: 'error',
							title: this.$t('ctf.alreadyDone.title'),
							text: this.$t('ctf.alreadyDone.text')
						})
					} else if(err.response.data.message == 'invalid flag'){
						this.$swal.fire({
							icon: 'error',
							title: this.$t('ctf.invalid.title'),
							text: this.$t('ctf.invalid.text')
						})
					}
				})
				.finally( () => {
					this.sendingFlag = false
				})
		},
		toggleAllCategories: function() {
			if(!this.arrChallenges.length) return []
			let onlyUniques = function(value, index, self){
		return self.indexOf(value) === index        
			}
			this.categoriesSelected = this.arrChallenges.map(c => c.category).filter(onlyUniques)
		},
		toggleCategory: function(cat){
			if(this.categoriesSelected.includes(cat)){
				let index = this.categoriesSelected.indexOf(cat)
				this.categoriesSelected.splice(index, 1)
			} else {
				this.categoriesSelected.push(cat) 
			}
		},
		isCategorySelected: function(cat){
			if(this.categoriesSelected.includes(cat)) return ''
				else return 'is-outlined'
		}
	}
}
</script>

