import Vue from 'vue'
import VueI18n from 'vue-i18n';
import en from './en';
import es from './es';

import moment from 'moment'

moment.locale(window.location.pathname.includes('/en/') ? 'en' : 'es')

Vue.use(VueI18n);

const translations = {
  en,
  es,
};

// Ready translated locale messages
// Create VueI18n instance with options
const i18n = new VueI18n({
  locale: window.location.pathname.includes('/en/') ? 'en' : 'es', // set locale
  fallbackLocale: 'es',
  messages: translations, // set locale messages
});



export { i18n };


