<script>
import userUtils from "@/mixins/userUtils";

export default {
  data() {
    return {
      isOpened: true,
      availableTickets: null,
      ticket: {}, // status & tier.
      // status: pending, purchasing
      paymentType: "usd",
      intervalId: null,
      isSending: false,
      requestCancel: false,
      inTicketWaitlist: null,
      hasTravelPlans: null,
      telegramUsername: null,
      reservationFile: null,
      reservationFileSavedTime: null,
    };
  },
  mixins: [userUtils],
  created() {
    this.sync();
    this.getStatus();
    this.getPrices();
    this.intervalId = setInterval(this.getStatus, 5000);
  },
  beforeDestroy() {
    if (this.intervalId) clearInterval(this.intervalId);
  },
  methods: {
    sync: function() {
      // this.paymentType = this.getValue(this.user.paymentType);
      this.inTicketWaitlist = this.getValue(this.user.inTicketWaitlist) || false;
      this.hasTravelPlans = this.getValue(this.user.hasTravelPlans) || false;
      this.telegramUsername = this.getValue(this.user.telegramUsername);
      this.reservationFileSavedTime = this.getValue(this.user.reservationFileSavedTime);
    },
    getPrices: function() {
      this.$http
        .get("/ticket/prices")
        .then(res => {
          this.availableTickets = res.data;
        })
        .catch(err => {
          console.error(err);
          this.$buefy.snackbar.open({
            message: this.$t("profile.tickets.getTicketPricesError"),
            type: "is-warning"
          });
        });
    },
    getStatus: function() {
      console.log("getStatus() update...");
      this.$http.get("/ticket/status").then(res => {
        if (res.data.status === "success") this.requestCancel = false;
        if (this.ticket.status == "success" && res.data.tier.includes("upg_")) {
          // started upgrade. Save this ticket as previousTicket
          this.$store.commit("updatePreviousTicketStatus", this.ticket);
        }
          this.ticket = res.data;
        if(res.data.status == null && res.data.tier == null) {
          this.$store.commit("updateTicketStatus", null);
        } else {
          this.$store.commit("updateTicketStatus", this.ticket);
        }
      });
    },
    saveWaitlistStatus() {
      function readFile(file){
        return new Promise((resolve, reject) => {
          var fr = new FileReader();  
          fr.onload = () => {
            resolve(fr.result)
          };
          fr.onerror = reject;
          fr.readAsDataURL(file);
        });
      }
      if(this.inTicketWaitlist && this.hasTravelPlans && !this.reservationFile && !this.reservationFileSavedTime) {
        this.$swal.fire({
          icon: 'error',
          title: this.$t('profile.alertError.title'),
          text: this.$t('profile.alertError.text')
        })
        return;
      }
      this.startLoading();
      this.isSending = true;
      const payload = {
        inTicketWaitlist: this.inTicketWaitlist,
        hasTravelPlans: this.hasTravelPlans,
        telegramUsername: this.isOptional(this.telegramUsername),
      };
      if(this.reservationFile) {
        readFile(this.reservationFile)
        .then((result) => {
          payload.reservationFile = result;
          this.saveUserInfo(payload)
        })
        .catch(err => {
          console.error(err);
            this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
      } else {
        this.saveUserInfo(payload)
      }
    },
    saveUserInfo(payload) {
      this.$http
        .post("/user/update", payload)
        .then(() => this.updateUser())
        .then(() => {
          this.reservationFile = null;
          this.sync();
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
            this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading();
          this.isSending = false;
        });
    },
    cancelPurchase() {
      this.$http
        .post("/ticket/cancel")
        .then(() => {
          this.requestCancel = false;
          this.getStatus();
          this.$buefy.snackbar.open({
            message: this.$t("profile.tickets.purchaseCanceled")
          });
        })
        .catch(err => {
          console.error(err);
          this.$buefy.snackbar.open({
            message: this.$t("profile.tickets.purchaseCancelError")
          });
        });
    },
    ticketHasDiscount(ticket){
      // if the ticket string includes the word awoo or apollo, it doesnt have discount
      // do it with regex
      const regex = /awoo|apollo/gi;
      return !regex.test(ticket);
      
      // return true;
    },
    getTierDescriptions(ticket) {
      const arr = this.$t("tickets.about.all.stuff");
      if (ticket === "general") return arr;
      if (ticket === "sponsor" || ticket === "upg_general_sponsor") {
        return arr.concat(this.$t("tickets.about.sponsor.stuff"));
      }
      if (
        ticket === "supersponsor" ||
        ticket === "upg_general_supersponsor" ||
        ticket === "upg_sponsor_supersponsor"
      ) {
        // const arr2 = arr.concat(this.$t('tickets.about.sponsor.stuff'));
        return arr.concat(this.$t("tickets.about.super.completeStuff"));
      }
      if (
        ticket === "upg_general_awoo" ||
        ticket === "upg_sponsor_awoo" ||
        ticket === "upg_supersponsor_awoo" ||
        ticket === "awoo"
      ) {
        return arr.concat(
          this.$t("tickets.about.super.completeStuff"),
          this.$t("tickets.about.awoo.about")
        );
      }
      if (
        ticket === "apollo" ||
        ticket === "upg_general_apollo" ||
        ticket === "upg_sponsor_apollo" ||
        ticket === "upg_supersponsor_apollo" ||
        ticket === "upg_awoo_apollo"
      ) {
        return arr.concat(
          this.$t("tickets.about.super.completeStuff"),
          this.$t("tickets.about.apollo.about")
        );
      }
    },
    isSoldOut: function(ticket){
      // return this.soldOutTickets.includes(ticket) || this.ticketsAreSoldOut
      return this.soldOutTickets.includes(ticket)
    }
  },
  computed: {
    baseTicketsArs() {
      if (!this.availableTickets) return {};
      const keys = Object.keys(this.availableTickets["ars"]);
      const baseTickets = {};
      keys.forEach(key => {
        if (!key.includes("upg_")) {
          baseTickets[key] = this.availableTickets["ars"][key];
        }
      });
      return baseTickets;
    },
    baseTicketsUsd() {
      if (!this.availableTickets) return {};
      const keys = Object.keys(this.availableTickets["usd"]);
      const baseTickets = {};
      keys.forEach(key => {
        if (!key.includes("upg_")) {
          baseTickets[key] = this.availableTickets["usd"][key];
        }
      });
      return baseTickets;
    },
    currencyShort() {
      if (!this.paymentType) return "";
      if (this.paymentType === "ars") return "AR$";
      return "USD";
    },
    hasBaseTicket() {
      let aux = ['general','sponsor','supersponsor','awoo']
      if (!this.ticket) return false;
      return aux.includes(this.ticket.tier);
    },
    availableUpgradesArs() {
      if (!this.ticket) return {};
      if (!this.availableTickets) return {};
      const keys = Object.keys(this.availableTickets["ars"]);
      const availables = {};
      keys.forEach(key => {
        if (key.includes(`upg_${this.ticket.tier}`)) {
          availables[key] = this.availableTickets["ars"][key];
        }
      });
      return availables;
    },
    availableUpgradesUsd() {
      if (!this.ticket) return {};
      if (!this.availableTickets) return {};
      const keys = Object.keys(this.availableTickets["usd"]);
      const availables = {};
      keys.forEach(key => {
        if (key.includes(`upg_${this.ticket.tier}`)) {
          availables[key] = this.availableTickets["usd"][key];
        }
      });
      return availables;
    },
    noMoreUpgrades() {
      const keys = Object.keys(this.availableUpgradesArs);
      return keys.length === 0;
    }
  },
  watch: {
    openedSection: function(newVal) {
      if (newVal) this.intervalId = setInterval(this.getStatus, 5000);
      else {
        clearInterval(this.intervalId);
        this.intervalId = setInterval(this.getStatus, 30000);
      }
    }
  }
};
</script>

