<template>
  <div>
    <h1 class="title has-text-centered">{{ $t("signup.title") }}</h1>
    <h1 class="subtitle has-text-centered">{{ $t("signup.subtitle") }}</h1>
    <h1
      class="title is-6 is-uppercase has-text-centered has-text-danger"
    >
      {{ $t("signup.requiredFields") }}
    </h1>
    <fieldset>
      <ValidationObserver ref="observer" v-slot="{ invalid }">
        <div class="field">
        <ValidationProvider
          :name="$t('signup.labelEmail')"
          :rules="{required: true, email: true}"
          v-slot="{errors, classes}"
        >
          <div class="control">
            <label class="label">{{ $t("signup.labelEmail") }}</label>
            <input
              required
              class="input"
              :class="classes"
              v-model="newUser.email"
              type="email"
              :placeholder="$t('signup.placeholderEmail')"
            />
          </div>
          <p class="help is-danger">{{ errors[0] }}</p>
        </ValidationProvider>
      </div>
      <div class="columns">
        <div class="column">
          <ValidationProvider
            :name="$t('signup.labelPassword')"
            :rules="{required: true, min: 8, alpha_dash: true}"
            v-slot="{errors}"
            vid="password"
          >              
          <div class="field">

          <label class="label">{{ $t("signup.labelPassword") }}</label>
          <div class="control">
            <input
              required
              class="input"
              v-model="newUser.password"
              type="password"
              :placeholder="$t('signup.placeholderPassword')"
            />
            <p class="help">{{ $t("signup.helpTextPassword") }}</p>
            <p class="help is-danger">{{ errors[0] }}</p>
          </div>
          </div>  
          </ValidationProvider>
        </div>
        <div class="column">
          <ValidationProvider
            :name="$t('signup.labelRepeatPassword')"
            :rules="{required: true, confirmed: 'password'}"
            v-slot="{errors}"
          >
          <div class="field">
            <label class="label">{{ $t("signup.labelRepeatPassword") }}</label>
            <div class="control">
              <input
                required
                class="input"
                v-model="newUser.repeatPassword"
                type="password"
                :placeholder="$t('signup.placeholderRepeatPassword')"
              />
              <p class="help">{{ $t("signup.helpTextRepeatPassword") }}</p>
              <p class="help is-danger">{{ errors[0] }}</p>
            </div>
          </div>
          </ValidationProvider>
        </div>
      </div>
      <div class="columns">
        <div class="column">
          <ValidationProvider
            :name="$t('signup.labelRealNames')"
            :rules="{required: true, alpha_spaces: true}"
            v-slot="{errors}"
          >
          <div class="field">
            <label class="label">{{ $t("signup.labelRealNames") }}</label>
            <div class="control">
              <input
                class="input"
                v-model="newUser.names"
                required
                type="text"
                :placeholder="$t('signup.placeholderRealNames')"
              />
            </div>
            <p class="help is-danger">{{ errors[0] }}</p>
          </div>
          </ValidationProvider>

        </div>
        <div class="column">
            <ValidationProvider
              :name="$t('signup.labelRealSurnames')"
              :rules="{required: true, alpha_spaces: true}"
              v-slot="{errors}"
            >
          <div class="field">
            <label class="label">{{ $t("signup.labelRealSurnames") }}</label>
            <div class="control">
              <input
                class="input"
                v-model="newUser.surnames"
                required
                type="text"
                :placeholder="$t('signup.placeholderRealSurnames')"
              />
            <p class="help is-danger">{{ errors[0] }}</p>
            </div>
          </div>
          </ValidationProvider>
        </div>
      </div>
      <div class="columns">
        <div class="column">
          <div class="field">
            <label class="label">{{ $t("signup.labelCountry") }}</label>
            <div class="control">
              <div class="select is-fullwidth">
                <select required v-model="newUser.country">
                  <option :value="null" disabled>
                    {{ $t("signup.placeholderCountry") }}
                  </option>
                  <option v-for="(v, k) in $t('countries')" :key="k" :value="k">
                    {{ v }}
                  </option>
                </select>
              </div>
            </div>
          </div>
        </div>
        <div class="column">
          <ValidationProvider
            :name="$t('signup.labelDocumentType')"
            :rules="{required: true}"
            v-slot="{errors}"
          >
            <div class="field">
              <label class="label">{{$t('signup.labelDocumentType')}}</label>
              <div class="control is-expanded">
                  <div class="select is-fullwidth">
                    <select v-model.lazy="newUser.documentType">
                      <option :value="null" disabled>{{$t('form.selectOne')}}</option>
                      <option
                        v-for="key in documentTypeOptions"
                        :key="key"
                        :value="key"
                      >{{$t('form.documentTypeOptions')[key]}}</option>
                    </select>
                  </div>
                  <span class="help is-danger">{{ errors[0] }}</span>
              </div>
            </div>
          </ValidationProvider>
        </div>
        <div class="column">
          <ValidationProvider
            :name="$t('signup.labelDocumentNumber')"
            :rules="{required: true}"
            v-slot="{errors}"
          >
            <div class="field">
              <label class="label">{{$t('signup.labelDocumentNumber')}}</label>
              <div class="control is-expanded">
                  <input
                    type="text"
                    v-model="newUser.documentNumber"
                    class="input"
                    @keyup.enter="submit"
                    :placeholder="$t('form.required')"
                  />
                  <span class="help is-danger">{{ errors[0] }}</span>
              </div>
            </div>
          </ValidationProvider>
        </div>
      </div>
      <div class="notification is-warning" v-if="signUpEmailFailed">
        <span>{{ $t("signup.textSignUpFail") }}</span>
      </div>
      <div class="buttons is-right">
        <button @click.prevent="doShowLogin" class="button is-white">
          {{ $t("signup.goBack") }}
        </button>
        <captcha
          sitekey="6Lf4T9IaAAAAAE2FUUkD39CTR9-rmTOomRStaqcq"
          :validate="checkSignUp"
          :callback="doSignUp"
          class="button is-primary"
          :class="{ 'is-loading': signUpEmailLoading }"
          type="submit"
          id="register-button"
          :disabled="signUpEmailLoading || invalid"
        >
          {{ $t("signup.registerButton") }}</captcha
        >
      </div>
      </ValidationObserver>
    </fieldset>
  </div>
</template>

<script>
import countriesUtils from "../../mixins/countriesUtils";
import Captcha from "./Captcha.vue";
export default {
  data() {
    return {
      newUser: {
        names: null,
        surnames: null,
        password: null,
        repeatPassword: null,
        email: null,
        country: null,
        documentNumber: null,
        documentType: null,
      },
      signUpEmailLoading: false,
      signUpEmailFailed: false,
    };
  },
  mixins: [countriesUtils],
  components: {
    Captcha,
  },
  computed: {
    isSpanish: function () {
      return window.location.pathname.indexOf("/en/") != 0;
    },
    documentTypeOptions() {
      return Object.keys(this.$t("form.documentTypeOptions"));
    }
  },
  methods: {
    checkSignUp: async function () {
      const result = await this.$refs.observer.validate();
      if (!result) {
        this.$swal.fire({
          icon: 'warning',
          title: this.$t('profile.alertInvalidFields.title'),
          text: this.$t('profile.alertInvalidFields.text')
        })
      }
      return result;
    },
    doShowLogin: function () {
      this.$emit("showLogIn");
    },
    doSignUp: function (recaptchaToken) {
      if (!recaptchaToken) {
        this.$swal.fire({
          icon: "error",
          title: "Oops...",
          text: this.$t("signup.alertRecaptchaTextFail"),
        });
        return;
      }
      this.signUpEmailLoading = true;
      this.signUpEmailFailed = false;
      this.$http
        .post("/user/register", {
          language: this.isSpanish ? "es" : "en",
          names: this.newUser.names,
          surnames: this.newUser.surnames,
          email: this.newUser.email.toLowerCase().trim(),
          country: this.newUser.country,
          password: this.newUser.password,
          documentType: this.newUser.documentType,
          documentNumber: this.newUser.documentNumber,
          // fursonaFullname: this.newUser.fursonaFullname,
          // fursonaDisplayName: this.newUser.fursonaDisplayName,
          // fursonaSpecies: this.newUser.fursonaSpecies,
          // fursonaPronoun: this.newUser.fursonaPronoun,
          captcha: recaptchaToken,
        })
        .then(() => {
          this.$emit("successSignUp", this.newUser.email.toLowerCase().trim());
          this.$swal.fire({
            icon: "success",
            title: this.$t("signup.alertSuccessTitle"),
            text: this.$t("signup.textSignUpSuccess", {
              userEmail: this.newUser.email.toLowerCase().trim(),
            }),
          });
        })
        .catch(() => {
          this.signUpEmailFailed = true;
          this.$swal.fire({
            icon: "error",
            title: "Woops...",
            text: this.$t("signup.textSignUpFail"),
          });
        })
        .finally(() => {
          this.signUpEmailLoading = false;
        });
    },
  },
};
</script>