<template>
  <section class="mb-2">
    <div class="py-2 box mb-0 has-background-success is-size-7" v-if="hasCompletedDataExtra">
      <p class="has-text-white">
        <i class="fas fa-check fa-fw"></i>&nbsp;{{$t('profile.extra.completed')}}
      </p>
    </div>
    <div class="py-2 box mb-0 has-background-warning is-size-7" v-else>
      <p class="has-text-black">
        <i class="fas fa-arrow-down fa-fw"></i>&nbsp;{{$t('profile.extra.notCompleted')}}
      </p>
    </div>
    <div class="box">
      <div class="is-flex is-flex-direction-row is-justify-content-space-between is-clickable" @click="isOpened = !isOpened">
        <p class="is-size-4 is-700">
          {{$t('profile.extra.title')}}<span v-if="hasCompletedDataExtra">&nbsp;&nbsp;<i class="fas fa-check-circle has-text-success fa-fw"></i></span>
        </p>
        <p class="is-size-4">
          <i class="fas" :class="isOpened ? 'fa-angle-up' : 'fa-angle-down'"></i>
        </p>
      </div>
      <div v-show="isOpened">
        <br>
        <div class="media">
          <div class="media-left">
            <i class="fas fa-question-circle fa-2x fa-fw"></i>
          </div>
          <div class="media-content">
            <h1 class="title is-5 is-marginless">{{$t('profile.extra.fursuiter.title')}}</h1>
            <p class="is-size-7">{{$t('profile.extra.fursuiter.note')}}</p>
            <br />
            <b-field>
              <b-radio-button
                v-model="fursuiter"
                :disabled="isSending"
                :native-value="false"
              >{{$t('form.optionNo')}}</b-radio-button>
              <b-radio-button
                v-model="fursuiter"
                :disabled="isSending"
                :native-value="true"
              >{{$t('form.optionYes')}}</b-radio-button>
            </b-field>
          </div>
        </div>
        <div class="media" v-if="showShirtInfo">
          <div class="media-left">
            <i class="fas fa-question-circle fa-2x fa-fw"></i>
          </div>
          <div class="media-content">
            <h1 class="title is-5 is-marginless">{{$t('profile.extra.shirt.title')}}</h1>
            <p class="is-size-7">{{$t('profile.extra.shirt.note')}}</p>
            <br />
            <b-field>
              <b-radio-button v-model="shirtSize" :disabled="isSending" native-value="XS">XS</b-radio-button>
              <b-radio-button v-model="shirtSize" :disabled="isSending" native-value="S">S</b-radio-button>
              <b-radio-button v-model="shirtSize" :disabled="isSending" native-value="M">M</b-radio-button>
              <b-radio-button v-model="shirtSize" :disabled="isSending" native-value="L">L</b-radio-button>
              <b-radio-button v-model="shirtSize" :disabled="isSending" native-value="XL">XL</b-radio-button>
              <b-radio-button v-model="shirtSize" :disabled="isSending" native-value="XXL">XXL</b-radio-button>
              <b-radio-button v-model="shirtSize" :disabled="isSending" native-value="XXXL">XXXL</b-radio-button>
            </b-field>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import userUtils from "@/mixins/userUtils";

export default {
  data() {
    return {
      isOpened: false,
      isSending: false,
      //data
      shirtSize: null,
      fursuiter: null
    };
  },
  created() {
    this.sync();
  },
  mixins: [userUtils],
  methods: {
    sync: function() {
      this.shirtSize = this.getValue(this.user.shirtSize);
      this.fursuiter = this.getValue(this.user.fursuiter);
    },
    saveShirtSize() {
      this.startLoading();
      this.isSending = true;
      const payload = {
        shirtSize: this.shirtSize
      };
      this.$http
        .post("/user/update", payload)
        .then(() => this.updateUser())
        .then(() => this.sync())
        .then(() => {
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading();
          this.isSending = false;
        });
    },
    saveFursuiter() {
      this.startLoading();
      this.isSending = true;
      const payload = {
        fursuiter: this.fursuiter
      };
      this.$http
        .post("/user/update", payload)
         .then(() => this.updateUser())
        .then(() => this.sync())
        .then(() => {
          this.$swal.fire({
            icon: 'success',
            title: this.$t('profile.alertSuccess.title'),
            text: this.$t('profile.alertSuccess.text')
          })
        })
        .catch(err => {
          console.error(err);
          this.$swal.fire({
            icon: 'error',
            title: this.$t('profile.alertError.title'),
            text: this.$t('profile.alertError.text')
          })
        })
        .finally(() => {
          this.stopLoading();
          this.isSending = false;
        });
    }
  },
  computed: {
    showShirtInfo: function(){
      if(this.hasTicket){
        if(this.user.ticket.status === 'pending' || this.user.ticket.status === 'purchasing'){
          if(
            this.user.previousTicket && (
              this.user.previousTicket.tier === 'sponsor'
              || this.user.previousTicket.tier === 'supersponsor'
              || this.user.previousTicket.tier === 'awoo'
              || this.user.previousTicket.tier === 'apollo'
              || this.user.previousTicket.tier === 'staff'
            )){
            return true
          }
        } else {
          if(
            this.user.ticket.tier === 'sponsor'
            || this.user.ticket.tier === 'supersponsor'
            || this.user.ticket.tier === 'awoo'
            || this.user.ticket.tier === 'apollo'
            || this.user.ticket.tier === 'staff'
          ){
            return true
          }
          return false
        }
      } 
      return false
    }
  },
  watch: {
    shirtSize: function(newVal) {
      const aux = this.user.shirtSize || null;
      if (aux !== newVal) this.saveShirtSize(newVal);
    },
    fursuiter: function(newVal) {
      const aux = this.user.fursuiter;
      console.log(aux);
      if (aux !== newVal && aux !== null) this.saveFursuiter(newVal);
    }
  }
};
</script>

<style lang="scss" scoped>
.field.has-addons.buttons-centered {
  justify-content: center;
}
</style>
