<template>
  <div class="media">
      <div class="media-left">
        <p :class="iconColor">
          <i class="fas fa-2x" :class="icon"></i>
        </p>
      </div>
      <div class="media-content">
        <p class="is-500" :class="titleColor">{{title}}</p>
        <p class="is-size-7" :class="textColor"><span v-if="done"><i class="fas fa-check-circle has-text-success"></i></span><slot></slot></p>
      </div>
    </div>
</template>

<script>
export default {
  props: ['done', 'title'],
  computed: {
    iconColor: function(){
      return this.done ? 'has-text-warning' : 'has-text-grey-light'
    },
    icon: function(){
      return this.done ? 'fa-award' : 'fa-question-circle'
    },
    titleColor: function(){
      return this.done ? '' : 'has-text-grey-light'
    },
    textColor: function(){
      return this.done ? '' : 'has-text-grey-light'
    }
  }
}
</script>