<script>
import userUtils from "@/mixins/userUtils";
import InputReadOnly from "@/components/utils/InputReadOnly";

export default {
  mixins: [userUtils],
  components: {
    InputReadOnly,
  },
  data() {
    return {
      names: null,
      surnames: null,
      birthday: null,
      documentType: null,
      documentNumber: null,
      sex: null,
      email: null,
      phone: null,
      mobile: null,
      country: null,
      province: null,
      city: null,
      zipCode: null,
      address: null,
      attendeeType: null,
      emergencyContact: null,
      emergencyPhone: null,
      hasMedsAllergies: null,
      customMedsAllergies: null,
      hasFoodAllergies: null,
      customFoodAllergies: null,
      hasOtherAllergies: null,
      customOtherAllergies: null,
      doingMeds: null,
      customDoingMeds: null,
      somethingElse: null,
      confirmVaccinationNotice: null,
      shirtSize: null,
      fursuiter: null,
      fursonaFullname: null,
      fursonaDisplayName: null,
      fursonaSpecies: null,
      fursonaPronoun: null,
      fursonaDescription: null,
      isSending: false,
      intervalId: null,
      ticket: null
    };
  },
  created() {
    this.sync();
    // if(user.ticket) this.ticket = user.ticket
    this.getStatus();
    this.intervalId = setInterval(this.getStatus, 60000);
    // this.intervalId = setInterval(this.getStatus, 1000);
  },
  destroyed() {
    clearInterval(this.intervalId);
  },
  methods: {
    sync() {
      this.names = this.getValue(this.user.names);
      this.surnames = this.getValue(this.user.surnames);
      this.birthday = this.getValue(this.user.birthday);
      this.documentType = this.getValue(this.user.documentType);
      this.documentNumber = this.getValue(this.user.documentNumber);
      this.sex = this.getValue(this.user.sex);
      this.email = this.getValue(this.user.email);
      this.phone = this.getValue(this.user.phone);
      this.mobile = this.getValue(this.user.mobile);
      this.country = this.getValue(this.user.country);
      this.province = this.getValue(this.user.province);
      this.city = this.getValue(this.user.city);
      this.zipCode = this.getValue(this.user.zipCode);
      this.address = this.getValue(this.user.address);
      this.attendeeType = this.getValue(this.user.attendeeType);
      this.paymentType = this.user.paymentType || null;
      this.confirmVaccinationNotice = this.getValue(this.user.confirmVaccinationNotice)
      this.emergencyContact = this.getValue(this.user.emergencyContact)
      this.emergencyPhone = this.getValue(this.user.emergencyPhone)
      this.hasMedsAllergies = this.getValue(this.user.hasMedsAllergies);
      this.customMedsAllergies = this.getValue(this.user.customMedsAllergies);
      this.hasFoodAllergies = this.getValue(this.user.hasFoodAllergies);
      this.customFoodAllergies = this.getValue(this.user.customFoodAllergies);
      this.hasOtherAllergies = this.getValue(this.user.hasOtherAllergies);
      this.customOtherAllergies = this.getValue(this.user.customOtherAllergies);
      this.doingMeds = this.getValue(this.user.doingMeds);
      this.customDoingMeds = this.getValue(this.user.customDoingMeds);
      this.somethingElse = this.getValue(this.user.somethingElse);
      this.shirtSize = this.getValue(this.user.shirtSize);
      this.fursuiter = this.getValue(this.user.fursuiter);
      this.fursonaFullname = this.getValue(this.user.fursonaFullname);
      this.fursonaDisplayName = this.getValue(this.user.fursonaDisplayName);
      this.fursonaSpecies = this.getValue(this.user.fursonaSpecies);
      this.fursonaPronoun = this.getValue(this.user.fursonaPronoun);
      this.fursonaDescription = this.getValue(this.user.fursonaDescription);
    },
    getStatus() {
      this.$http.get("/ticket/status").then(res => {
        this.ticket = res.data;
      });
    }
  },
  computed: {
    showShirtInfo: function(){
      if(this.hasTicket){
        if(this.user.ticket.status === 'pending' || this.user.ticket.status === 'purchasing'){
          if(this.user.previousTicket && (this.user.previousTicket.tier === 'sponsor' || this.user.previousTicket.tier === 'supersponsor')){
            return true
          }
        } else {
          if(this.user.ticket.tier === 'sponsor' || this.user.ticket.tier === 'supersponsor'){
            return true
          }
          return false
        }
      } 
      return false
    }
  }
};
</script>

