import Vue from 'vue'
import { extend, configure, ValidationProvider, ValidationObserver } from 'vee-validate'
import { required, email, min, alpha_dash, alpha_spaces, confirmed } from "vee-validate/dist/rules";
import { i18n } from './i18n'


Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);

configure({
  // this will be used to generate messages.
  defaultMessage: (field, values) => {
    return i18n.t(`validation.${values._rule_}`, values);
  }
});

// Install rules and message.
extend("required", required);
extend("email", email);
extend("min", min);
extend("alpha_dash", alpha_dash);
extend("alpha_spaces", alpha_spaces);
extend("confirmed", confirmed);
extend("validDate", {
  validate(value) {
    if (!value) return null;
    let d = new Date(value)
    if (Object.prototype.toString.call(d) === "[object Date]") {
      // it is a date
      if (isNaN(d.getTime())) {  // d.valueOf() could also work
        // date is not valid
        return false
      } else {
        // date is valid
        return true
      }
    } else {
      // not a date
      return false
    }
  },
  message: (_, values) => i18n.t('validation.regex', values)
  }
);